const allowedExt = ["gif","jpeg","jpg","png","rtf", "doc","docx","odt","txt","mp3","wav","wma","jar","ggb","swf","jnlp"];

const validate = values => {
  const errors = {}

  // Title
  if (!values.title) {
    errors.title = 'O campo  obrigatrio'
  }

  // Author
  if (!values.author) {
    errors.author = 'O campo  obrigatrio'
  }

  // Email
  if (!values.email) {
    errors.email = 'O campo  obrigatrio'
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = 'E-mail inserido no  vlido';
  }

  // Organization
  if (!values.organization) {
    errors.organization = 'O campo  obrigatrio'
  }

  // Tags
  if (!values.tags) {
    errors.tags = 'O campo  obrigatrio'
  } else if (values.tags.length==0 || values.tags.length > 5) {
    errors.tags = 'Deve ter entre 1 e 5 palavras-chave'
  }

  // Formats
  if (!values.format) {
    errors.format = 'O campo  obrigatrio'
  }

  // File
  if (!values.isOnline && !values.file && ( values.format && values.format.type!="video")){
    errors.file = 'Campo  obrigatrio'
  }else if (!values.isOnline && values.file && values.file.size && values.file.size>1000000) {
    errors.file = 'Ficheiro no deve exceder os 1 MB'
  }else if(!values.isOnline && values.format && values.format.type!="video" && values.file && values.file.extension && allowedExt.indexOf(values.file.extension.toLowerCase())<0){
    errors.file = `Extenso .${values.file.extension} no  permitida`
  }

  // Duration
  if (values.format && values.format.type=="video" && !values.duration){
    errors.duration = 'Campo  obrigatrio'
  }

  // Embed
  if (values.format && values.format.type=="video" && !values.embed && !values.link){
    errors.embed = 'Campo  obrigatrio'
  }

  // Link
  if (values.isOnline && (!values.link && !values.embed)){
    errors.embed = 'Campo  obrigatrio'
  }

  // Access modes
  if (!values.access || values.access.length==0) {
    errors.access = 'O campo  obrigatrio'
  }

  // Tech Resources
  if (!values.techResources) {
    errors.techResources = 'O campo  obrigatrio'
  } else if (values.techResources.length < 20) {
    errors.techResources = 'Deve ter pelo menos 20 caracteres'
  } else if (values.techResources.length > 300) {
    errors.techResources = 'Apenas deve conter no mximo 300 caracteres'
  }

  // Description
  if (!values.description) {
    errors.description = 'O campo  obrigatrio'
  } else if (values.description.length < 20) {
    errors.description = 'Deve ter pelo menos 20 caracteres'
  } else if (values.description.length > 300) {
    errors.description = 'Apenas deve conter no mximo 300 caracteres'
  }

  return errors
}

export default validate