const validate = values => {
  const errors = {}

  // Subjects
  if (!values.subjects || values.subjects.length==0) {
    errors.subjects = 'Campo  obrigatrio'
  }

  // Domains
  if (values.hasDomains && (!values.domains || values.domains.length==0)) {
    errors.domains = 'Campo  obrigatrio'
  }

  // Years
  if (!values.years || values.years.length==0) {
    errors.years = 'Campo  obrigatrio'
  }

  // Languages
  if (!values.language) {
    errors.language = 'Campo  obrigatrio'
  }

  // Op Proposal
  if (!values.op_proposal) {
    errors.op_proposal = 'O campo  obrigatrio'
  } else if (values.op_proposal.length < 20) {
    errors.op_proposal = 'Deve ter pelo menos 20 caracteres'
  } else if (values.op_proposal.length > 800) {
    errors.op_proposal = 'Apenas deve conter no mximo 800 caracteres'
  }

  // Op proposal author
  if (!values.op_proposal_author) {
    errors.op_proposal_author = 'Campo  obrigatrio'
  }

  // Accepted terms
  if (!values.accept_terms) {
    errors.accept_terms = 'Deve aceitar os termos e condies para criar o recurso'
  }

  return errors
}

export default validate